var axios = require('axios');

var _require = require('./utils'),
    ServiceResponse = _require.ServiceResponse,
    ServiceError = _require.ServiceError;

function pushtx(tx) {
  return axios.post('https://api.blockcypher.com/v1/btc/test3/txs/push', JSON.stringify({
    tx: tx
  })).then(function (response) {
    return new ServiceResponse(response.response.status, response.response.data);
  }).catch(function (error) {
    throw new ServiceError(error.response.status, error.response.data.error);
  });
}

function pullMetadata() {
  return axios.get('https://api.blockcypher.com/v1/btc/test3').then(function (response) {
    return new ServiceResponse(response.response.status, response.response.data);
  }).catch(function (error) {
    throw new ServiceError(error.response.status, error.response.data.error);
  });
}

module.exports = {
  pushtx: pushtx,
  pullMetadata: pullMetadata
};