var blockcypher = require('./blockcypher');

function pushtx(tx) {
  return blockcypher.pushtx(tx).then(function (response) {
    return response;
  }).catch(function (error) {
    throw error;
  });
}

function pullMetadata() {
  return blockcypher.pullMetadata().then(function (response) {
    return response;
  }).catch(function (error) {
    throw error;
  });
}

module.exports = {
  pushtx: pushtx,
  pullMetadata: pullMetadata
};